import Head from 'next/head'

import { Header, HeaderButton, Dropdown, DropdownItem } from '../components/layout/Header';
import { Container } from '../components/layout/Container';

import { MdSettings as SettingsIcon } from "react-icons/md";
import { FaUser as UserIcon, FaPlus as PlusIcon } from "react-icons/fa";

import { CSSTransition} from "react-transition-group";

function RecursiveDItem (props) {
    return (
        <>
            <DropdownItem>lmao</DropdownItem>
            <DropdownItem>{props.n}</DropdownItem>
            <DropdownItem dropdown={RecursiveDItem}>{props.n+"1"}</DropdownItem>
        </>
    );
}

export default function Home() {
    return (
        <div>
            <Head>
                <title>Huansohn</title>
            </Head>

            <Header>
                <HeaderButton icon={<PlusIcon/>}></HeaderButton>
                <HeaderButton icon={<UserIcon/>}>
                    <Dropdown>
                        <DropdownItem>GAMER</DropdownItem>
                        <DropdownItem 
                            dropdown={[[<DropdownItem>lmao</DropdownItem>]]}
                        >Open scndry</DropdownItem>
                    </Dropdown> 
                </HeaderButton>
                <HeaderButton icon={<SettingsIcon/>}></HeaderButton>
            </Header>
            <Container>
                <Dropdown>
                    <DropdownItem>GAMER</DropdownItem>
                        <DropdownItem 
                            dropdown={[[<DropdownItem>lmao</DropdownItem>]]}
                        >Open scndry</DropdownItem>
                    </Dropdown> 
            </Container>
        </div>
    )
}
